<script setup lang="ts">
import { ref, onMounted, onUnmounted } from 'vue'
import { useNotificationStore } from '@/stores/notifications'
import { websocketManager, useWebSocket } from '@/services/websocket'
import DashboardHeader from '@/components/layout/DashboardHeader.vue'
import { 
  CpuChipIcon, 
  XMarkIcon, 
  CheckIcon, 
  MagnifyingGlassIcon, 
  ArchiveBoxIcon 
} from '@heroicons/vue/24/outline'

const notifications = useNotificationStore()
const { connect, disconnect, isConnected } = useWebSocket()

// ÂìçÂ∫îÂºèÊï∞ÊçÆ
const pipelineData = ref(null)
const loading = ref(false)
const error = ref('')
const autoRefresh = ref(false)
const refreshInterval = ref(null)
const showFeaturePreview = ref(false)

// APIÂü∫Á°ÄURL
const API_BASE = 'http://localhost:5000/api'

// Ê≠•È™§ÂÆö‰πâ
const initializationSteps = [
  { id: 'step_1_database_initialization', name: 'Êï∞ÊçÆÂ∫ìÂàùÂßãÂåñ' },
  { id: 'step_2_component_initialization', name: 'ÁªÑ‰ª∂ÂàùÂßãÂåñ' },
  { id: 'step_3_async_evaluator_initialization', name: 'ÂºÇÊ≠•ËØÑ‰º∞Âô®ÂàùÂßãÂåñ' }
]

const enhancementSteps = [
  { id: 'step_4_unified_cache_vectorization', name: 'Áªü‰∏ÄÁºìÂ≠òÂêëÈáèÂåñ' },
  { id: 'step_5_faiss_vector_retrieval', name: 'FAISSÂêëÈáèÊ£ÄÁ¥¢' },
  { id: 'step_6_association_network_expansion', name: 'ÂÖ≥ËÅîÁΩëÁªúÊãìÂ±ï' },
  { id: 'step_7_history_dialogue_aggregation', name: 'ÂéÜÂè≤ÂØπËØùËÅöÂêà' },
  { id: 'step_8_weight_ranking_deduplication', name: 'ÊùÉÈáçÊéíÂ∫èÂéªÈáç' },
  { id: 'step_9_final_context_assembly', name: 'ÊúÄÁªà‰∏ä‰∏ãÊñáÊûÑÂª∫' }
]

const storageSteps = [
  { id: 'step_10_llm_response_generation', name: 'LLMÂìçÂ∫îÁîüÊàê' },
  { id: 'step_11_immediate_dialogue_storage', name: 'Âç≥Êó∂ÂØπËØùÂ≠òÂÇ®' },
  { id: 'step_12_async_llm_evaluation', name: 'ÂºÇÊ≠•LLMËØÑ‰º∞' },
  { id: 'step_13_save_evaluation_results', name: '‰øùÂ≠òËØÑ‰º∞ÁªìÊûú' },
  { id: 'step_14_auto_association_creation', name: 'Ëá™Âä®ÂÖ≥ËÅîÂàõÂª∫' }
]

// Â∑•ÂÖ∑ÂáΩÊï∞
const formatStepName = (stepId: string): string => {
  const stepMap: { [key: string]: string } = {
    'step_1_database_initialization': 'Êï∞ÊçÆÂ∫ìÂàùÂßãÂåñ',
    'step_2_component_initialization': 'ÁªÑ‰ª∂ÂàùÂßãÂåñ',
    'step_3_async_evaluator_initialization': 'ÂºÇÊ≠•ËØÑ‰º∞Âô®ÂàùÂßãÂåñ',
    'step_4_unified_cache_vectorization': 'Áªü‰∏ÄÁºìÂ≠òÂêëÈáèÂåñ',
    'step_5_faiss_vector_retrieval': 'FAISSÂêëÈáèÊ£ÄÁ¥¢',
    'step_6_association_network_expansion': 'ÂÖ≥ËÅîÁΩëÁªúÊãìÂ±ï',
    'step_7_history_dialogue_aggregation': 'ÂéÜÂè≤ÂØπËØùËÅöÂêà',
    'step_8_weight_ranking_deduplication': 'ÊùÉÈáçÊéíÂ∫èÂéªÈáç',
    'step_9_final_context_assembly': 'ÊúÄÁªà‰∏ä‰∏ãÊñáÊûÑÂª∫',
    'step_10_llm_response_generation': 'LLMÂìçÂ∫îÁîüÊàê',
    'step_11_immediate_dialogue_storage': 'Âç≥Êó∂ÂØπËØùÂ≠òÂÇ®',
    'step_12_async_llm_evaluation': 'ÂºÇÊ≠•LLMËØÑ‰º∞',
    'step_13_save_evaluation_results': '‰øùÂ≠òËØÑ‰º∞ÁªìÊûú',
    'step_14_auto_association_creation': 'Ëá™Âä®ÂÖ≥ËÅîÂàõÂª∫'
  }
  return stepMap[stepId] || stepId
}

const getPhaseColor = (phase: string): string => {
  const colors = {
    'initialization': '#10b981', // green
    'query_enhancement': '#3b82f6', // blue
    'storage_evaluation': '#8b5cf6' // purple
  }
  return colors[phase] || '#6b7280'
}

const getPhaseStatusClass = (status: string): string => {
  const classes = {
    'completed': 'bg-green-100 text-green-800',
    'running': 'bg-blue-100 text-blue-800',
    'idle': 'bg-gray-100 text-gray-800',
    'failed': 'bg-red-100 text-red-800'
  }
  return classes[status] || 'bg-gray-100 text-gray-800'
}

const getPhaseStatusText = (status: string): string => {
  const texts = {
    'completed': 'Â∑≤ÂÆåÊàê',
    'running': 'ËøêË°å‰∏≠',
    'idle': 'Á©∫Èó≤',
    'failed': 'Â§±Ë¥•'
  }
  return texts[status] || status
}

const getStepCardClass = (stepId: string): string => {
  if (!pipelineData.value?.current_step) {
    return 'bg-gray-50 border-gray-200'
  }
  
  if (pipelineData.value.current_step === stepId) {
    return 'bg-blue-50 border-blue-300 shadow-md'
  }
  
  const stepData = pipelineData.value.step_status[stepId]
  if (stepData && stepData.success_rate > 0.8) {
    return 'bg-green-50 border-green-200'
  }
  
  return 'bg-gray-50 border-gray-200'
}

const getStepStatusIcon = (stepId: string): string => {
  if (pipelineData.value?.current_step === stepId) {
    return 'text-blue-600'
  }
  
  const stepData = pipelineData.value?.step_status[stepId]
  if (stepData && stepData.success_rate > 0.8) {
    return 'text-green-600'
  }
  
  return 'text-gray-400'
}

const getStepStatusEmoji = (stepId: string): string => {
  if (pipelineData.value?.current_step === stepId) {
    return '‚ö°'
  }
  
  const stepData = pipelineData.value?.step_status[stepId]
  if (stepData && stepData.success_rate > 0.8) {
    return '‚úÖ'
  }
  
  return '‚≠ï'
}

// Âä†ËΩΩÊµÅÁ®ãÊï∞ÊçÆ
const loadPipelineData = async () => {
  loading.value = true
  error.value = ''
  
  try {
    const response = await fetch(`${API_BASE}/pipeline/status`)
    
    if (!response.ok) {
      const errorData = await response.json()
      throw new Error(errorData.error || `HTTP ${response.status}`)
    }
    
    const data = await response.json()
    pipelineData.value = data
    
    if (!autoRefresh.value) {
      notifications.add({
        type: 'success',
        title: 'ÊµÅÁ®ãÊï∞ÊçÆÊõ¥Êñ∞',
        message: `Ê¥ªË∑É‰ºöËØù: ${data.active_sessions}`
      })
    }
    
  } catch (err: any) {
    error.value = err.message || 'Âä†ËΩΩÂ§±Ë¥•'
    pipelineData.value = null
    
    if (!autoRefresh.value) {
      notifications.add({
        type: 'error',
        title: 'Âä†ËΩΩÂ§±Ë¥•',
        message: error.value
      })
    }
  } finally {
    loading.value = false
  }
}

// ÂàáÊç¢Ëá™Âä®Âà∑Êñ∞/WebSocketÊ®°Âºè
const toggleAutoRefresh = async () => {
  autoRefresh.value = !autoRefresh.value
  
  if (autoRefresh.value) {
    // ÂºÄÂêØWebSocketÂÆûÊó∂Êõ¥Êñ∞Ê®°Âºè
    try {
      if (!isConnected()) {
        await connect()
      }
      
      // ËÆ¢ÈòÖÊµÅÁ®ãÁä∂ÊÄÅÊõ¥Êñ∞
      websocketManager.subscribePipeline()
      
      // ËÆæÁΩÆWebSocket‰∫ã‰ª∂ÁõëÂê¨
      websocketManager.on('pipeline_status_update', handleWebSocketUpdate)
      
      notifications.add({
        type: 'success',
        title: 'WebSocketÂÆûÊó∂Êõ¥Êñ∞Â∑≤ÂºÄÂêØ',
        message: 'Â∑≤ËøûÊé•Âà∞ÂÆûÊó∂ÁõëÊéßÊúçÂä°'
      })
      
    } catch (error) {
      // WebSocketËøûÊé•Â§±Ë¥•ÔºåÈôçÁ∫ßÂà∞ËΩÆËØ¢Ê®°Âºè
      console.warn('WebSocketËøûÊé•Â§±Ë¥•ÔºåÈôçÁ∫ßÂà∞ËΩÆËØ¢Ê®°Âºè:', error)
      autoRefresh.value = false
      startPollingMode()
    }
  } else {
    // ÂÅúÊ≠¢WebSocketÁõëÂê¨
    websocketManager.off('pipeline_status_update', handleWebSocketUpdate)
    
    // ÂÅúÊ≠¢ËΩÆËØ¢Ê®°Âºè
    if (refreshInterval.value) {
      clearInterval(refreshInterval.value)
      refreshInterval.value = null
    }
    
    notifications.add({
      type: 'info',
      title: 'ÂÆûÊó∂Êõ¥Êñ∞Â∑≤ÂÖ≥Èó≠',
      message: 'Â∑≤ÂÅúÊ≠¢Ëá™Âä®Êõ¥Êñ∞'
    })
  }
}

// ÂêØÂä®ËΩÆËØ¢Ê®°ÂºèÔºàÈôçÁ∫ßÊñπÊ°àÔºâ
const startPollingMode = () => {
  refreshInterval.value = setInterval(loadPipelineData, 3000)
  notifications.add({
    type: 'info',
    title: 'ËΩÆËØ¢Ê®°ÂºèÂ∑≤ÂêØÂä®',
    message: 'ÊØè3ÁßíËá™Âä®Êõ¥Êñ∞ÊµÅÁ®ãÊï∞ÊçÆ'
  })
}

// WebSocketÊõ¥Êñ∞Â§ÑÁêÜÂô®
const handleWebSocketUpdate = (data: any) => {
  pipelineData.value = data
  console.log('üìä Êî∂Âà∞WebSocketÊµÅÁ®ãÁä∂ÊÄÅÊõ¥Êñ∞:', data)
}

// ÁªÑ‰ª∂ÊåÇËΩΩÂíåÂç∏ËΩΩ
onMounted(async () => {
  console.log('PipelineViewer mounted')
  
  // È¶ñÊ¨°Âä†ËΩΩÊï∞ÊçÆ
  await loadPipelineData()
  
  // Â∞ùËØïËøûÊé•WebSocket
  try {
    await connect()
    console.log('‚úÖ WebSocketËøûÊé•ÊàêÂäü')
  } catch (error) {
    console.warn('‚ö†Ô∏è WebSocketËøûÊé•Â§±Ë¥•ÔºåÂ∞Ü‰ΩøÁî®HTTP API:', error)
  }
})

onUnmounted(() => {
  // Ê∏ÖÁêÜÂÆöÊó∂Âô®
  if (refreshInterval.value) {
    clearInterval(refreshInterval.value)
  }
  
  // ÁßªÈô§WebSocketÁõëÂê¨Âô®
  websocketManager.off('pipeline_status_update', handleWebSocketUpdate)
  
  console.log('PipelineViewer unmounted')
})
</script>